import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/shared/api/login.service';
import { loginbl } from 'src/app/shared/BL/login.bl';
import { login } from 'src/app/shared/interfaces/login';
@Component({
    selector: 'app-page-profile',
    templateUrl: './page-profile.component.html',
    styleUrls: ['./page-profile.component.sass']
})
export class PageProfileComponent {
    constructor(private loginservice: LoginService, private router: Router, private login_bl_obj: loginbl) { }
    logindetails: login = new login();
    message: string = ""
    ngOnInit() { this.getprofile() };
    message: string = "";


    getprofile() {
        console.log("in get profile page component...")
        if (sessionStorage.getItem("pkey")) {
            this.logindetails.customerId = Number(sessionStorage.getItem("pkey"));

            this.login_bl_obj.userprofile(this.logindetails).then(u => {
                if (u.status) {
                    console.log(u)
                    //  console.log(this.logindetails)
                    this.logindetails = u.returnValue
                    // console.log(this.logindetails)
                }
                else
                    alert(u.message)
            });
        }
        else {
            alert("Please Login");

        }

    }

    saveprofile() {
        console.log("in get profile page component...")
        if (sessionStorage.getItem("pkey")) {
            this.logindetails.typeCustomer='C'
            this.login_bl_obj.register_user(this.logindetails).then(u => {

                console.log(u.message)

                alert(u.message)
            });
        }
        else {
            alert("Please Login");

        }
    }
}
